
body {
  margin: 0;
}
#resume {
  padding: 60px 0;
  background-color: #eee;
  color: #4e959f;
  font-family: "Microsoft JhengHei", Helvetica, Arial, sans-serif;
}
#landscape {
  position: fixed;
  top: 0;
  display: none;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, .8);
  color: #fff;
  z-index: 99;
}
.fullpage {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #c1e4e9;
  text-align: center;
}
.page {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
  transition: background .3s ease-in, -webkit-transform .5s ease-out;
  transition: transform .5s ease-out, background .3s ease-in;
  transition: transform .5s ease-out, background .3s ease-in, -webkit-transform .5s ease-out;
  overflow: hidden;
}
.current, .pre, .next {
  opacity: 1;
}
.current {
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
.pre {
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
  background: rgba(0,0,0,.3);
}
.next {
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
  background: rgba(0,0,0,.3);
}
.container {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 100%;
}
.title {
  margin-top: 0;
  font-size: 3rem;
  color: #fff;
  text-shadow: 1px 2px #38a1db;
}

