}

.wrapper {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  max-height: 100%;
}*/
a {
  text-decoration: none;
  color: #4e959f;
}
@media screen and (orientation: landscape) {
#landscape {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
}



